.conx{
    border-radius: 30px;
    width: 300px;
    height: 300px;
    background-image: url(../img/1.webp);
    position: relative;
    background-position: left bottom;
    box-shadow: 0px 0px 20px;
    margin: 30px;
}
.s{
    position: absolute;
    width: 100%;
    height: 40%;
    background-color: aliceblue;
    bottom: 0px;
    border-radius: 0px 0px 30px 30px;
    display: flex;
    justify-content: center;
   

    
}
.ss{
    color: #000000;
    margin: auto;
    font-size: 20px;
     margin: auto;
     font-size: 20px;
    
    
    
    
}
.conx:hover{
    border-radius: 30px;
    width: 320px;
    height: 320px;
    background-image: url(../img/1.webp);
    position: relative;
    background-position: left bottom;
    box-shadow: 0px 0px 20px;
}

.s:hover{
    position: absolute;
    width: 100%;
    height: 50%;
    background-color: aliceblue;
    bottom: 0px;
    border-radius: 0px 0px 30px 30px;
    display: flex;
   

    
}
.vr{
    text-align: center;
    
